.custom-btn,
.contact-btn {
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 45px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: linear-gradient(to right, #242f40, #057af0, #3498db);
    outline: none;
}

.contact-btn:hover {
    color: white !important;
}

.btn,
.button-contact {
    background: linear-gradient(to right, #242f40, #030d9e, #3498db);
    line-height: 42px;
    padding: 0;
    border: none;
}

.btn span,
.button-contact span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: 0px 20px;
}

.btn:before,
.btn:after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 0;
    background: #05299e;
    box-shadow: linear-gradient(to right, #057af0, #3498db);
    transition: all 0.3s ease;
}

.button-contact:before,
.button-contact:after {
    position: absolute;
    content: "";
    right: 0;
    bottom: 0;
    background: white;
    box-shadow: white;
    transition: all 0.3s ease;
}

.btn:before,
.button-contact:before {
    height: 0%;
    width: 2px;
}

.btn:after,
.button-contact:after {
    width: 0%;
    height: 2px;
}

.btn:hover,
.button-contact:hover {
    color: #05299e;
    background: transparent;
}

.btn:hover:before,
.button-contact:hover:before {
    height: 100%;
}

.btn:hover:after,
.button-contact:hover:after {
    width: 100%;
}

.btn span:before,
.btn span:after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    background: #05299e;
    box-shadow: linear-gradient(to right, #030d9e, #0328a1);
    transition: all 0.3s ease;
}

.button-contact span:before,
.button-contact span:after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    background: white;
    box-shadow: white;
    transition: all 0.3s ease;
}

.btn span:before,
.button-contact span:before {
    width: 2px;
    height: 0%;
}

.btn span:after,
.button-contact span:after {
    height: 2px;
    width: 0%;
}

.btn span:hover:before,
.button-contact span:hover:before {
    height: 100%;
}

.button-contact span:hover:after,
.btn span:hover:after {
    width: 100%;
}

